import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { EventProvider } from '../../providers/event/event';
import { ProfileProvider } from '../../providers/profile/profile';

/**
 * Generated class for the MeusanunciosPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-meusanuncios',
  templateUrl: 'meusanuncios.html',
})
export class MeusanunciosPage {

  public Meusanuncios: Array<any>;
  constructor(public navCtrl: NavController, public eventProvider:
    EventProvider, public navParams: NavParams,  public profileProvider: ProfileProvider) { }

/*
var ref = firebase.database().ref("dinosaurs");
ref.orderByChild("height").equalTo(25).on("child_added", function(snapshot) {
  console.log(snapshot.key);
});
*/

      ionViewDidLoad() {
        if (true) {this.eventProvider.getMeusAnuncios().on('value', MeusanunciosSnapshot => {
          this.Meusanuncios = [];
          MeusanunciosSnapshot.forEach(snap => {
            this.Meusanuncios.push({
              id: snap.key,
              Nome: snap.val().Nome,
              Preco: snap.val().Preco,
              Data_inicial: snap.val().Data_inicial,
              Data_final: snap.val().Data_final,
              ID_Dono: snap.val().ID_Dono,          
            });
            return false;
          });
        });} 
  }
  
  goToEventDetail(eventId):void {
    this.navCtrl.push('EditarAnuncioPage', { 'eventId': eventId });
    }
}




